/* src/app/features/notes/notes-page/notes-page.scss */
.feature-card {
  max-width: 700px;
  margin: 2.5rem auto 0 auto;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.08), 0 1.5px 6px 0 rgba(0, 0, 0, 0.04);
  padding: 2.5rem 2.5rem 2rem 2.5rem;
  animation: fadeIn 0.7s cubic-bezier(0.4, 0, 0.2, 1);
}
.feature-card-header {
  display: flex;
  align-items: center;
  gap: 0.9rem;
  margin-bottom: 1.5rem;
}
.feature-card-header mat-icon {
  font-size: 2.2rem;
  height: 2.2rem;
  width: 2.2rem;
  min-width: 2.2rem;
  min-height: 2.2rem;
  color: #1976d2;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  vertical-align: middle;
  position: relative;
  top: 0;
}
.feature-card-header h1 {
  font-size: 1.7rem;
  font-weight: 700;
  margin: 0;
  color: #222;
}
.markdown-editor {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.editor-field {
  width: 100%;
}
.editor-actions {
  display: flex;
  gap: 1rem;
}
.preview {
  background: #fafafa;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
  padding: 1.5rem;
  min-height: 120px;
  font-size: 1.1rem;
}
@media (max-width: 700px) {
  .feature-card {
    padding: 1.2rem 0.7rem 1.2rem 0.7rem;
    max-width: 98vw;
  }
  .feature-card-header h1 {
    font-size: 1.1rem;
  }
  .markdown-editor {
    max-width: 100%;
    padding: 0 1rem;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
/*# sourceMappingURL=notes-page.css.map */
